@font-face {
  font-family: "Clear Sans";
  src: url("../../assets/fonts/ClearSans-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Clear Sans";
  src: url("../../assets/fonts/ClearSans-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
body {
  margin: 0;
  font-family: "Clear Sans", "Helvetica", "sans-serif";
  background-color: #faf8ef;
}

.ui {
  margin: auto;
  display: grid;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  grid-template-columns: 500px;
}
@media screen and (max-width: 520px) {
  .ui {
    grid-template-columns: calc(100vw - 40px);
  }
}
.ui .header {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  padding: 40px 0;
  gap: 10px;
}
.ui .header > div:first-of-type {
  flex: 1;
  color: #776e65;
}
.ui .header > div:first-of-type h1 {
  font-size: 80px;
  font-weight: bold;
  margin: 0;
}
.ui .header > div:first-of-type p {
  font-size: 18px;
  margin: 0;
}
.ui .header > div:first-of-type p a {
  color: #776e65;
  font-weight: bold;
}
.ui .header > div:first-of-type p a:hover {
  color: #2cc3ff;
}
.ui .header > div:last-of-type {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  margin-top: 15px;
}
.ui .header > div:last-of-type > div {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4px;
}
.ui .header > div:last-of-type > div > div {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #bbada0;
  border-radius: 3px;
  padding: 8px 12px 4px;
}
.ui .header > div:last-of-type > div > div h1 {
  margin: 0;
  color: #eee4da;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 13px;
}
.ui .header > div:last-of-type > div > div span {
  font-size: 25px;
  color: white;
  line-height: 25px;
  font-weight: bold;
}
.ui .header > div:last-of-type button {
  font-family: "Clear Sans", "Helvetica", "sans-serif";
  border: none;
  outline: none;
  cursor: pointer;
  background-color: #8f7a66;
  color: #f9f6f2;
  padding: 5px 20px;
  font-weight: bold;
  font-size: 18px;
  border-radius: 3px;
}
@media screen and (max-width: 480px) {
  .ui .header {
    flex-direction: column;
    padding-bottom: 15px;
  }
  .ui .header > div:last-of-type {
    flex-direction: column-reverse;
    align-items: stretch;
    gap: 15px;
  }
}
.ui .board {
  background-color: #bbada0;
  border-radius: 6px;
  padding: 15px;
  position: relative;
  width: calc(500px - calc(2 * 15px));
  height: calc(500px - calc(2 * 15px));
}
@media screen and (max-width: 520px) {
  .ui .board {
    width: calc(calc(100vw - 40px) - calc(2 * 15px));
    height: calc(calc(100vw - 40px) - calc(2 * 15px));
  }
}
.ui .board .back {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 15px;
}
.ui .board .back div {
  background-color: rgba(238, 228, 218, 0.35);
  width: 100%;
  border-radius: 3px;
}
.ui .board .front {
  width: calc(100% - calc(2 * 15px));
  height: calc(100% - calc(2 * 15px));
  position: absolute;
  top: 0;
  left: 0;
  margin: 15px;
  z-index: 1;
}
.ui .board .front .tile {
  width: calc(25% - calc(0.75 * 15px));
  height: calc(25% - calc(0.75 * 15px));
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  border-radius: 3px;
  position: absolute;
  top: calc(calc(25% - calc(0.75 * 15px)) / 2);
  left: calc(calc(25% - calc(0.75 * 15px)) / 2);
  transform: translate(-50%, -50%);
  -webkit-animation-name: pop;
          animation-name: pop;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  box-shadow: 0 0 30px 10px rgb(243 215 116/56%), inset 0 0 0 1px rgb(255 255 255/33%);
  background-color: #edc22e;
  font-size: 35px;
  color: #f9f6f2;
}
@-webkit-keyframes pop {
  from {
    transform: translate(-50%, -50%) scale(0);
    opacity: 0;
  }
  to {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
}
@keyframes pop {
  from {
    transform: translate(-50%, -50%) scale(0);
    opacity: 0;
  }
  to {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
}
